import React, { Component } from "react";
import { OwnIcon } from '@icons/module-icons';
import { ButtonIconsSecondary } from "@module-button/button-icons/index.secondary.button";
import { ButtonActionsLike } from "@module-button/buttons/buttons-actions/index.buttons.actions.like";
import { ButtonActionsShare } from "@module-button/buttons/buttons-actions/index.buttons.actions.share";
import { FooterPropType, FooterPropDefault } from '@interfaces/layout-type/type-layouts';

class PartFeedsReadRightHeaderButton extends Component<FooterPropType> {
  static defaultProps = FooterPropDefault;
  render(){
    const { props } = this; 
    return(
      <div className={"right_button_container d_flex justify_content_between align_items_center"}> 
        <ButtonActionsLike render = { ( childProps ) => (
          <ButtonIconsSecondary className={"notification_btn"}>
            <OwnIcon.IconHeart isActive = { childProps.isActive } className={"icons"} />
          </ButtonIconsSecondary>
        )} />
        <ButtonActionsShare render = { () => (
          <ButtonIconsSecondary  className={"notification_btn"}>
            <OwnIcon.IconShare className={"icons"} />
          </ButtonIconsSecondary>
        )}/>
      </div>
    )
  }
}

export default PartFeedsReadRightHeaderButton;
