import { ParsingCommentsDataBundlePropType } from "@interfaces/data-type/type.pages-comments";
import FlatList from "@module-flat-list/index.flat-list";
import { FlatItemList, formatRupiah } from "@module-flat-list/index.item-list";
import { CallbackRender, Container, Dispatch, FooterMobileGlobal, HeaderFeedsDetailsPages, LazyFoxInputBox, LazyFoxModalWindow, OwnIcon, StoragesSystem,  } from "index-module";
import React, { Component } from "react";
import { connect } from "react-redux";
import { RouteComponentProps, withRouter } from "react-router-dom";
import { ButtonMain } from "@module-button/buttons/index.main.button";
import { getDate } from "@module-flat-list/index.item-laporan-list";


export interface ScreenChartPropTypeSmall {
  StoragesSystem: any,
  setStorage?: (obj: ParsingCommentsDataBundlePropType) => void
}

export type ScreenChartPropType = ScreenChartPropTypeSmall & RouteComponentProps;



export interface ScreenChartStateType {
  chart: any,
  key: any,
  searchKey: any,
  money?: any,
  isReady?: any,
}

const FormLoginLogicPropDefault : ScreenChartPropTypeSmall = {
  StoragesSystem: {
    chart: [

    ]
  },
}

class ScreenChart extends Component<ScreenChartPropType,  ScreenChartStateType> {
  static defaultProps = FormLoginLogicPropDefault;
  private openModalWindow = React.createRef<LazyFoxModalWindow>()
  constructor(props: any){
    super(props)
    this.state = {
      chart: props.StoragesSystem?.chart || [],
      key: 'title',
      searchKey: "",
      money: {}
    }
  }
  onScroll = () => {


  }

  componentDidUpdate = (prevProps: ScreenChartPropType) => {
    if(this.props.StoragesSystem !== prevProps.StoragesSystem){
      this.setState({
        chart: this.props.StoragesSystem?.chart || [],
        key: 'any',
        searchKey: '',
        money: 0,
        isReady: false,
      })
    }
  }

  onCheckout = async() => {
    if(this.state.isReady){
      let data : any = {};
      const chart = this.props.StoragesSystem.chart || [];
      let report = this.props.StoragesSystem.report || [];
      const catalog = this.props.StoragesSystem.catalog || [];
      for (let i = 0; i < chart.length; i++) {
        const newItem = chart[i];
        const tempCatalog = catalog[newItem.tempIndex];
        if(tempCatalog){
          catalog[newItem.tempIndex] = {
            ...tempCatalog,
            stok: tempCatalog.stok - newItem.total,
          }
        }
        
      }
      const date = new Date().getTime(); 
      // let date: any = new Date(); 
      //   date.setDate(22)
      // date = date.getTime(); 
      
      data = {
        chart,
        totalPayment: `${this.getTotaPayment(true)}`,
        money: `${this.state.money.value}`,
        date,
        textDate: `${getDate(date, "text")} ${getDate(date, "hour")}`,
        hour: `${getDate(date, "hour")}`
      }
      report = [
        data,
        ...report,
      ];
      // report.push(data)
      if(this.props.setStorage){
        this.props.setStorage({data:[], type: 'chart'});
        this.props.setStorage({data:report, type: 'report'});
        this.props.setStorage({data:catalog, type: 'catalog'});
      }
      this.setState({
        isReady: false,
        money: {}
      })
    }
    
  }

  getTotaPayment = (value? : boolean) => {
    const { chart } =  this.props.StoragesSystem || [];
    let price = 0;
    for (let i = 0; i < chart.length; i++) {
      const item = chart[i];
      price = price + (parseInt(item.price) * parseInt(item.total));
    }
    if(value){
      return price;
    }
    return formatRupiah(price.toString())
  }

  

  onOpen = () => {
    if(this.openModalWindow.current){
      this.openModalWindow.current?.show()
    }
  }

  onDismiss = () => {
    this.setState({isReady: true},() => {
      if(this.openModalWindow){
        this.openModalWindow.current?.dismiss()
      }
    })
  }

  onChangeMoney = (e: any) => {
    this.setState({
      money: e
    })
  }

  getKembalian = (value: any) => {
    if(value < 0){
      return 'Uang Kurang'
    }
    return formatRupiah(value.toString());
  } 

  render(){
    const { props, state } = this;
    const totalPayment: any = this.getTotaPayment(true);
    return(
      <>
        <HeaderFeedsDetailsPages title = {"Keranjang"} />
        <Container className={"login_page_wrapper"}>
          <section className={"content_value content_value_login"}>
            
            <FlatList data = {state.chart} onScroll={this.onScroll} render= { ({items, index} : CallbackRender) => (
              <>
                <FlatItemList {...props} item = {items} index = {index} key = {`${index}-chart-${items.id}`} isChart = {true} />
              </>
            )} />
            { state.chart.length > 0 &&
              <div className="total_payment">
                <div className="total_payment-inside">
                  <h3>Total Yang Harus Dibayar</h3>
                  <h1>{formatRupiah(totalPayment.toString())}</h1>
                </div>
                <ButtonMain isDark = {true} onClick = {this.onOpen} >
                  <span>Bayar dan Masukan Ke Laporan</span>
                </ButtonMain>
              </div>
            }
            {state.chart.length < 1 &&
              <div className="error-list-empty">
                <div className="error-list-empty-inside">
                  <OwnIcon.IconAlert />
                  <h1>Keranjang anda kosong</h1>
                </div>
              </div>
            }
          </section>
        </Container>
        <FooterMobileGlobal {...props} activePage = {"keranjang"}  />
        <LazyFoxModalWindow onDismiss={this.onCheckout} className={"modal-window-center customse"} ref = {this.openModalWindow} useHeader = {true} headerTitle = {"PERINGATAN!!!"}>
          <div className="success-info">
            <div className="text-success">
              <h3>Anda akan melakukan checkout dan memasukan data ke laporan</h3>
              <br/>
              <LazyFoxInputBox errorText = {`Masukan Pecahan uang`} title = {"Masukan Pecahan uang"} type = {"number"} key={'money'} onChange = {this.onChangeMoney} />
              
              <span>Pembayaran Menggunakan uang Pecahan</span>
              <h2>{formatRupiah(state.money.value || 0)}</h2>
              <br/>
              <span>Total Pembayaran</span>
              <h2>{formatRupiah(totalPayment.toString())}</h2>
              <br/>
              <span>Kembalian</span>
              <h2>{this.getKembalian((state.money.value - totalPayment))}</h2>
            </div>
            <ButtonMain isDark = {true} onClick = {this.onDismiss}>
              <span>Proses</span>
            </ButtonMain>
          </div>
        </LazyFoxModalWindow>
      </>
    )
  }
}

const mapStateToProps = (state: any) => ({
  StoragesSystem: state.StoragesSystem,
})
const mapDispatchToProps = (dispatch: Dispatch ) => ({
  setStorage: ({ data, type }: ParsingCommentsDataBundlePropType) => dispatch( new StoragesSystem().setStorage({ data, type }) ),
})

export default withRouter(connect( mapStateToProps, mapDispatchToProps )( ScreenChart ));
