import { ParsingCommentsDataBundlePropType } from "@interfaces/data-type/type.pages-comments";
import { Container, Dispatch, FooterMobileGlobal, HeaderFeedsDetailsPages, LazyFoxModalWindow,  StoragesSystem,  } from "index-module";
import React, { Component } from "react";
import { connect } from "react-redux";
import { RouteComponentProps, withRouter } from "react-router-dom";
import { OwnImage } from "@img/module-images";
import { ButtonMain } from "@module-button/buttons/index.main.button";
import { ApiBody, BASE_URL, ServerRequest } from "@utils/utils-server-request";


export interface ScreenInfoPropTypeSmall {
  StoragesSystem: any,
  setStorage?: (obj: ParsingCommentsDataBundlePropType) => void
}

export type ScreenInfoPropType = ScreenInfoPropTypeSmall & RouteComponentProps;



export interface ScreenInfoStateType {
  report: any,
  key: any,
  searchKey: any,
  money?: any,
  isReady?: any,
  isFound?: any, isError?: any, isFetching?: any,
}

const FormLoginLogicPropDefault : ScreenInfoPropTypeSmall = {
  StoragesSystem: {
    report: [

    ]
  },
}

class ScreenInfo extends Component<ScreenInfoPropType,  ScreenInfoStateType> {
  static defaultProps = FormLoginLogicPropDefault;
  private openModalWindow = React.createRef<LazyFoxModalWindow>()
  constructor(props: any){
    super(props)
    this.state = {
      report: props.StoragesSystem?.report || [],
      key: 'title',
      searchKey: "",
      money: {}
    }
  }

  componentDidUpdate = (prevProps: ScreenInfoPropType) => {
    if(this.props.StoragesSystem !== prevProps.StoragesSystem){
      this.setState({
        report: this.props.StoragesSystem?.report || [],
      })
    }
  }

  backUpData = async() => {
    const json = await JSON.stringify(this.props.StoragesSystem);
    const blob= await new Blob([json],{type:'application/json'});
    const href = await URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = href;
    const date = new Date();
    link.download = `backup-${date.getTime()}.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }

  getData = () => {
    const config: ApiBody  = {
      endpoint: '/data.json',
    }
    if(!this.state.isFetching){
      this.setState({isFetching: true})
      new ServerRequest(BASE_URL).get( config, ({data, isFound, isError, isFetching}) =>{
        if(isFound){
          this.setState({ isFound, isError, isFetching})
          if(this.props.setStorage){
            this.props.setStorage({data: data.chart, type: 'chart'});
            this.props.setStorage({data: data.report, type: 'report'});
            this.props.setStorage({data: data.catalog, type: 'catalog'});
          }
        }else if(isError){
          this.setState({ isFound, isError, isFetching})
        }
      })
    }
  }

  render(){
    const { props, state } = this;
    return(
      <>
        <HeaderFeedsDetailsPages title = {"Annisee app"} />
        <Container className={"login_page_wrapper"}>
          <section className={"content_value content_value_login"}>
            <div className="error-list-empty">
              <div className="error-list-empty-inside main-logo-container">
                <div className="logo-anisy">
                  <img src={OwnImage.MainIcon} />
                </div>
                <h1>Annisee app</h1>
                <span>Version 1.0.2</span>
              </div>
            </div>
            <div className="error-list-empty">
              <div className="error-list-empty-inside main-logo-container custom-info">
                <p>Made with Love</p>
                <p>Dedicated for my wonderful women</p>
                <h2>Annisa Rakhma Diantri</h2>
                <p>"No Pain No Gain Sweetheart"</p>
                <p>Hope it can help you anytime anywhere</p>
              </div>
            </div>
            <div className="error-list-empty">
              <div className="error-list-empty-inside main-logo-container custom-info backup-info">
                <p>Backup Data</p>
                <ButtonMain isDark = {true} onClick = {this.backUpData} >
                  <span>Backup</span>
                </ButtonMain>
              </div>
            </div>
            <div className="error-list-empty">
              <div className="error-list-empty-inside main-logo-container custom-info backup-info">
                <p>Update Data, Hati hati, hanya saat pertama pakai saja {state.isFound && <h3>Update Berhasil</h3>}</p>
                { state.isFetching &&
                  <h2>Silahkan Menunggu...</h2>
                }
                { !state.isFetching &&
                  <ButtonMain isDark = {true} onClick = {this.getData} >
                    <span>Update</span>
                  </ButtonMain>
                }
              </div>
            </div>
          </section>
        </Container>
        <FooterMobileGlobal {...props}  activePage = {"anisy"}  />
      </>
    )
  }
}

const mapStateToProps = (state: any) => ({
  StoragesSystem: state.StoragesSystem,
})
const mapDispatchToProps = (dispatch: Dispatch ) => ({
  setStorage: ({ data, type }: ParsingCommentsDataBundlePropType) => dispatch( new StoragesSystem().setStorage({ data, type }) ),
})

export default withRouter(connect( mapStateToProps, mapDispatchToProps )( ScreenInfo ));
