import React from 'react';
import ReactDOM from 'react-dom';
import '@assets/sass/main.scss'
import { MainApps, RouterPropType } from 'index-module';
import ScreenHome from '@screens/screen-home';
import screenAdd from '@screens/screen-add';
import screenChart from '@screens/screen-chart';
import screenReport from '@screens/screen-report';
import screenInfo from '@screens/screen-info';

const dataRouteMap: RouterPropType = [
  {
    component: ScreenHome,
    exact: true,
    path: '/'
  },
  {
    component: screenAdd,
    exact: true,
    path: '/add'
  },
  {
    component: screenAdd,
    exact: true,
    path: '/edit/:contentId/:indexId'
  },
  {
    component: screenChart,
    exact: true,
    path: '/keranjang'
  },
  {
    component: screenReport,
    exact: true,
    path: '/laporan'
  },
  {
    component: screenInfo,
    exact: true,
    path: '/annisee'

  },
  {
    component: screenInfo,
    exact: true,
    path: '/*'
  }
];

class Apps extends React.Component{
  render(){
    return(
      <MainApps renderFrom={"browser"} dataRouteMap={dataRouteMap} />
    )
  }
}

export default Apps;
