import { ParsingCommentsDataBundlePropType } from "@interfaces/data-type/type.pages-comments";
import {  Container, DataArray, Dispatch, HeaderFeedsDetailsPages, LazyFoxInputBox, LazyFoxModalWindow, LazyFoxModalWindowPropType, StoragesSystem,  } from "index-module";
import React, { Component } from "react";
import { connect } from "react-redux";
import jsonQuery  from 'json-query';
import { withRouter } from "react-router-dom";
import { ScreenHomePropType, ScreenHomePropTypeSmall } from "./screen-home";
import { ButtonMain } from "@module-button/buttons/index.main.button";
import { uuidv4 } from "@utils/utils-globals";

export interface ScreenAddStateType {
  catalog: any,
  key: any,
  value: any,
  isError: boolean,
  contentId?: any,
  indexId?: any
}

const FormLoginLogicPropDefault : ScreenHomePropTypeSmall = {
  StoragesSystem: {
    catalog: []
  },
}

const ENUM_INPUT = [
  { title: "Nama Barang / Obat", type: "null", idKey: 'title'},
  { title: "Nama lain / orang biasa menyebutnya", type: "null", idKey: 'alias', },
  { title: "Harga satuan / pack / kaplet", type: "number", idKey: 'price', },
  { title: "Stok Yang Tersedia", type: "number", idKey: 'stok', },
  { title: "Keterangan / Deskripsi", type: "null", idKey: 'desc', isMultiline: true, row: 10},
  { title: "Cara Minum / Pakai", type: "null", idKey: 'indikasi', isMultiline: true, row: 10 },
];

const ENUM_WHITE_LIST: any = {'title': true, 'alias': true, 'price': true, 'stok': true};
const ENUM_NUMBER: any = {'price': true, 'stok': true};

const generateValue = (obj: any = {}) => {
  let newObject = {};
  for (const key in obj) {
    newObject = {
      ...newObject,
      [key]: {
        isError: false,
        value: obj[key]
      }
    };
  }
  return newObject
}
class ScreenAdd extends Component<ScreenHomePropType,  ScreenAddStateType> {
  static defaultProps = FormLoginLogicPropDefault;
  constructor(props: any){
    super(props)
    this.state = {
      catalog: props.StoragesSystem?.catalog || [],
      key: 'title',
      isError: false,
      contentId: props.match?.params?.contentId,
      indexId: props.match?.params?.indexId,
      value: generateValue(props.StoragesSystem?.catalog[props.match?.params?.indexId])
    }
  }
  private openModalWindow = React.createRef<LazyFoxModalWindow>()
  
  onChange = (e: any) => {
    if(ENUM_NUMBER[e.idKey]){
      e.value = parseInt(e.value);
    }
    this.setState({
      isError: false,
      value: {
        ...this.state.value,
        [e.idKey]: e
      }
    })
  }

  onSubmit = (e: any) => {
    console.log(e)
  }

  onSave = (e: any) => {
    let data : any = {};
    let stateValue = this.state.value;
    for (let i = 0; i < ENUM_INPUT.length; i++) {
      const item = stateValue[ENUM_INPUT[i].idKey] ;
      if(item?.isError){
        this.setState({isError: true})
       return; 
      }else{
        if(ENUM_WHITE_LIST[ENUM_INPUT[i].idKey]){
          if(!item){
            stateValue = {
              ...stateValue,
              [ENUM_INPUT[i].idKey]: {isError: true, value: undefined}
            }
            this.setState({value: stateValue, isError: true})
            return
          }
        }
        data = {
          ...data,
          id: uuidv4(),
          [ENUM_INPUT[i].idKey]: item?.value || "-",
        }
      }
    }
    let catalog = this.props.StoragesSystem.catalog || [];
    if(this.state.indexId){
      catalog[this.state.indexId] = data
    }else{
      catalog = [
        data,
        ...catalog,
      ]
    }
    if(this.props.setStorage){
      this.props.setStorage({data:catalog, type: 'catalog'});
    }
    if(this.openModalWindow){
      this.openModalWindow.current?.show()
    }
    
  }

  onBack = () => {
    if(this.openModalWindow.current){
      this.openModalWindow.current?.dismiss()
    }
  }

  onDismiss = () => {
    if( this.props.history.length > 1 ){
      this.props.history.goBack();
    }else{
      this.props.history.push('/');
    } 
  }

  render(){
    const { props, state } = this;
    return(
      <>
        <HeaderFeedsDetailsPages  title = {state.indexId ? "Perubahan Data" :"Tambah Daftar"} />
        <Container className={"login_page_wrapper"}>
          <section className={"content_value content_value_login"}>
            {ENUM_INPUT?.map((itemProps, index) =>(
              <LazyFoxInputBox key={index} {...itemProps} {...state.value[itemProps.idKey]} onChange = {this.onChange} onSubmit = {this.onSubmit} />
            ))}
            { state.isError &&
              <div className="error-info">
                <span>Maaf lengkapi data yang penting terlebih dahulu</span>
              </div>
            }
            <ButtonMain isDark = {true} onClick = {this.onSave} >
              {!state.indexId&&
                <span>Simpan Data</span>
              }
              { state.indexId &&
                <span>Simpan Perubahan</span>
              }
              
            </ButtonMain>
            <LazyFoxModalWindow onDismiss={this.onDismiss} useCloseOnClickBackground = {false} className={"modal-window-center"} ref = {this.openModalWindow} useHeader = {true} headerTitle = {state.indexId ? "Edit Info" :"Add Info"}>
              <div className="success-info">
                <div className="text-success">
                  <h2>{state.indexId ? "Pengubahan data berhasil" : "Penambahan data berhasil"}</h2>
                </div>
                <ButtonMain isDark = {true} onClick = {this.onBack}>
                  <span>Lihat List</span>
                </ButtonMain>
              </div>
            </LazyFoxModalWindow>
          </section>
        </Container>
      </>
    )
  }
}

const mapStateToProps = (state: any) => ({
  StoragesSystem: state.StoragesSystem,
})
const mapDispatchToProps = (dispatch: Dispatch ) => ({
  setStorage: ({ data, type }: ParsingCommentsDataBundlePropType) => dispatch( new StoragesSystem().setStorage({ data, type }) ),
})

export default withRouter(connect( mapStateToProps, mapDispatchToProps )( ScreenAdd ));
