import { ParsingCommentsDataBundlePropType } from "@interfaces/data-type/type.pages-comments";
import FlatList from "@module-flat-list/index.flat-list";
import { FlatItemList } from "@module-flat-list/index.item-list";
import { CallbackRender, Container, Dispatch, FooterMobileGlobal, HeaderGlobalPages, OwnIcon, StoragesSystem,  } from "index-module";
import React, { Component } from "react";
import { connect } from "react-redux";
import jsonQuery  from 'json-query';
import { RouteComponentProps, withRouter } from "react-router-dom";


export interface ScreenHomePropTypeSmall {
  StoragesSystem: any,
  setStorage?: (obj: ParsingCommentsDataBundlePropType) => void
}

export type ScreenHomePropType = ScreenHomePropTypeSmall & RouteComponentProps;



export interface ScreenHomeStateType {
  catalog: any,
  key: any,
  searchKey: any
}

const FormLoginLogicPropDefault : ScreenHomePropTypeSmall = {
  StoragesSystem: {
    catalog: [
    ]
  },
}

const ENUM_KEY:any = {
  'nama': 'title',
  'name': 'title',
  'desc': 'desc',
  'harga': 'price',
  'stok': 'stok',
  'alias': 'alias',
  'cara': 'indikasi',
}
const ENUM_KEY_REGEX:any = {
  'title': 'title',
  'desc': 'desc',
  'alias': 'alias',
  'cara': 'indikasi',
}
class ScreenHome extends Component<ScreenHomePropType,  ScreenHomeStateType> {
  static defaultProps = FormLoginLogicPropDefault;
  constructor(props: any){
    super(props)
    this.state = {
      catalog: props.StoragesSystem?.catalog || [],
      key: 'title',
      searchKey: "",
    }
  }
  onScroll = () => {


  }

  componentDidUpdate = (prevProps: ScreenHomePropType) => {
    if(this.props.StoragesSystem !== prevProps.StoragesSystem){
      this.setState({
        catalog: this.props.StoragesSystem?.catalog || [],
        key: 'any',
        searchKey: '',
      })
    }
  }
  private timeOut: any= undefined;
  onChange = (e: any) => {
		clearTimeout(this.timeOut)
    this.timeOut = setTimeout(() => {
      this.onChangeRequest(e)
    }, 500)
	}

  onChangeRequest = async(e: any) => {
    if(e.value.length > 0){
      let targetKey = e.value.match(/(nama:)|(name:)|(desc:)|(harga:)|(stok:)|(alias:)|(cara:)/i);
      let queryLang = e.value;
      let query: any;
      if(targetKey){
        targetKey = e.value.split(':')[0];
        queryLang = queryLang.replace(targetKey+":",'');
        targetKey = ENUM_KEY[targetKey];
        if(targetKey === 'price'){
          queryLang = queryLang.replace('.','');
        }
        if(ENUM_KEY_REGEX[targetKey]){
          queryLang = "~/"+queryLang+"/i";
        }
        query =  await jsonQuery(("catalog[*"+targetKey+queryLang+"]"), {
          data: this.props.StoragesSystem,
          allowRegexp: true,
        }) 
      }else{
        query =  await jsonQuery(("catalog[*"+'title'+"~/"+queryLang+"/i]"), {
          data: this.props.StoragesSystem,
          allowRegexp: true,
        }) 
        if(query.value.length < 1){
          query =  await jsonQuery(("catalog[*"+'alias'+"~/"+queryLang+"/i]"), {
            data: this.props.StoragesSystem,
            allowRegexp: true,
          }) 
          if(query.value.length < 1){
            query =  await jsonQuery(("catalog[*"+'indikasi'+"~/"+queryLang+"/i]"), {
              data: this.props.StoragesSystem,
              allowRegexp: true,
            }) 
          }
        }
      }
      this.setState({
        searchKey: query.value,
        catalog: query.value
      })
    }else{
      this.setState({
        catalog: this.props.StoragesSystem.catalog
      })
    }
  }
  

  render(){
    const { props, state } = this;
    // console.log(props);
    
    return(
      <>
        <HeaderGlobalPages onChange = {this.onChange}/>
        <Container className={"login_page_wrapper"}>
          <section className={"content_value content_value_login"}>
            
            <FlatList data = {state.catalog} onScroll={this.onScroll} render= { ({items, index} : CallbackRender) => (
              <>
                <FlatItemList {...props} item = {items} index = {index} key = {`${index}-index-${items.id}`} />
              </>
            )} />
            {state.catalog.length < 1 &&
              <div className="error-list-empty">
                <div className="error-list-empty-inside">
                  <OwnIcon.IconAlert />
                  <h1>Obat anda kosong</h1>
                </div>
              </div>
            }
          </section>
        </Container>
        <FooterMobileGlobal {...props}  />
      </>
    )
  }
}

const mapStateToProps = (state: any) => ({
  StoragesSystem: state.StoragesSystem,
})
const mapDispatchToProps = (dispatch: Dispatch ) => ({
  setStorage: ({ data, type }: ParsingCommentsDataBundlePropType) => dispatch( new StoragesSystem().setStorage({ data, type }) ),
})

export default withRouter(connect( mapStateToProps, mapDispatchToProps )( ScreenHome ));
