interface FlatListStateType{
  isActive?: boolean,
  isOpen: any,
  isDelete?: any,
  value: any,
  isChart?: boolean,
  alreadyAdded?: boolean,
  isGoto?: boolean,
  isError?: boolean,
}

const FlatListPropDefault : any = {

} 

export class FlatItemList extends React.Component<any, FlatListStateType>{
  constructor( props: any ){
    super( props );
    this.state = {
      isOpen: false,
      isDelete: false,
      alreadyAdded: false,
      value: {
        value: 1,
        isError: props.item.stok > 0 ? false : true 
      }
    }
  }
  private openModalWindow = React.createRef<LazyFoxModalWindow>()
  private openModalWindowChart = React.createRef<LazyFoxModalWindow>()
  public static defaultProps = FlatListPropDefault;
  

  stok = (value: any) => {
    if(value > 0){
      return value
    }
    return 'habis'
  }
  getClassName = (value: any) => {
    if(value > 0){
      return 'masih'
    }
    return 'habis'
  }

  

  getPrice = (total: any, price: string) => {
    return `${formatRupiah((parseInt(total) * parseInt(price)).toString())}`
  }

  onOpen = () => {
    this.setState({isOpen: !this.state.isOpen})
  }

  onEdit = () => {
    this.props.history.push({
      pathname: `/edit/${this.props.item?.id}/${this.props.index}`
    })
  }



  onBack = () => {
    if(this.openModalWindow.current){
      this.openModalWindow.current?.dismiss()
    }
  }

  onOpenPupup = () => {
    if(this.openModalWindow.current){
      this.openModalWindow.current?.show()
    }
  }

  onOpenPupupChart = () => {
    if(this.openModalWindowChart.current){
      this.openModalWindowChart.current?.show()
    }
  }

  onDelete = (e: any) => {
    if(this.openModalWindow){
      this.setState({isDelete: true}, () => {
        this.openModalWindow.current?.dismiss()
      })
    }
  }

  onDismiss = () => {
    const { state } = this;
    this.setState({
      isGoto: false,
      isDelete: false,
      value: {
        value: 1,
        isError: false
      }
    }, () => {
      if(state.isDelete){
        const { props} = this;
        const key = props.isChart ? 'chart' : 'catalog'
        let catalog = this.props.StoragesSystem[key] || [];
        catalog.splice(props.index, 1);
        if(this.props.setStorage){
          this.props.setStorage({data:catalog, type: key});
        }
      }
      if(state.isGoto){
        this.props.history.push({
          pathname: `/keranjang`
        })
      }
    })
  }

  onChange = (e: any) => {
    if(e.value){
      if(parseInt(e.value) > parseInt(this.props.item.stok)){
        e.isError = true;
      }else{
        e.isError = false;
      }
      this.setState({
        isError: false,
        value: e
      })
    }
  }

  onAddToChart = (e: any) => {
    const { props, state} = this;
    if(state.value.isError){
      this.setState({isError: true})
      return;
    }
    let chart = this.props.StoragesSystem.chart || [];
    // chart.splice(props.index, 1);
    let isFound = false;
    for (let i = 0; i < chart.length; i++) {
      const tempItem = chart[i];
      if(props.item.id === tempItem.tempId){
        chart[i] = {
          ...chart[i],
          ...props.item,
          total: state.value.value
        }
        isFound = true;
        break;
      }
    }
    if(!isFound){
      chart.push({
        ...props.item,
        tempId: props.item.id,
        total: state.value.value,
        tempIndex: props.index,
        id: uuidv4()
      })
    }
    
    if(this.props.setStorage){
      this.props.setStorage({data: chart, type: 'chart'});
    }
    this.setState({
      alreadyAdded: true 
    })
    // if(this.openModalWindowChart){
    //   this.openModalWindowChart.current?.dismiss();
    // }
  }

  goToKeranjang = () => {
    if(this.openModalWindowChart){
      this.openModalWindowChart.current?.dismiss();
    }
    this.setState({isGoto: true})
  }

  render(){
    const { props, state } = this;
    return (
      <div className={"item-list-flat-list"} key = {props.key}>
        <div className={"item-list-flat-list-inside"}>
          { (!props.isChart || props.isHide) &&
            <div className={"medicine-title"} onClick = {this.onOpen}>
              <span className='numbering'>{(props.index+1)}</span>
              <span>{props.item.title}</span>
            </div>
          }
          <div className={"medicine-content"}>
            <Icons />
            <div className={'main-contents'}>

              <div className={"info"} onClick = {this.onOpen}>
                { !props.isChart &&
                  <>
                    <span className={"alias"}>{props.item?.alias}</span>
                    <span className={"price"}>Harga: <span>{formatRupiah(props.item?.price)}</span></span>
                    <span className={"stok"}>Stok: <span className={this.getClassName(props.item?.stok)}>{this.stok(props.item?.stok)}</span></span>
                  </>
                }
                { (props.isChart) &&
                  <>
                    {props.isHide &&
                      <span className={"alias"}>{props.item?.alias}</span>
                    }
                    {!props.isHide &&
                      <span className={"alias"}>{props.item.title} ({props.item?.alias})</span>
                    }
                    <span className={"price"}>Harga Satuan: <span>{formatRupiah(props.item?.price)}</span></span>
                    <span className={"stok"}>Sisa Stok: <span className={this.getClassName(props.item?.stok)}>{this.stok(props.item?.stok - props.item?.total)}</span></span>
                  </>
                }

                { props.isChart &&
                  <>
                    <span className={"alias"}>Total Yang Dibeli: <span className={"habis"}>{props.item?.total}</span> </span>
                    <span className={"alias"}>Harga Akhir: {this.getPrice(props.item?.total, props.item?.price)}</span>
                  </>
                }
              </div>
              { !state.isOpen && !props.isChart &&
                <div className={"lihat"} onClick = {this.onOpen}>Lihat Descripsi {">"}</div>
              }
              { state.isOpen && !props.isChart &&
                <div className={"descriptions"} onClick = {this.onOpen}>
                  <span>Deskripsi:</span>
                  <p>{props.item?.desc}</p>
                  <span>Cara Pakai:</span>
                  <p>{props.item?.indikasi}</p>
                </div>
              }
              <div className={"editor-container"}>
                { !props.isChart &&
                  <>
                    <span className={"button-chart"}>
                      <ButtonMain isDark = {true} onClick = {this.onOpenPupupChart}>
                        <span>Beli</span>
                      </ButtonMain>
                    </span>
                    <span onClick={ this.onEdit}>
                      <EditIcon />
                    </span>
                  </>

                }
                {!props.isHide &&

                  <span onClick={ this.onOpenPupup}>
                    <Trash />
                  </span>
                }
              </div>
            </div>
          </div>
        </div>
        
        <LazyFoxModalWindow onDismiss = {this.onDismiss} className={"modal-window-center"} ref = {this.openModalWindow} useHeader = {true} headerTitle = {"PERINGATAN !!!"}>
          <div className="success-info">
            <div className="text-success">
              <h2>Apakah anda yakin ingin menghapus {props.item.title} ?</h2>
            </div>
            <div className='d_flex justify_content_between'>
              <ButtonMain className='dark-key' isDark = {true} onClick = {this.onBack}>
                <span>Batalkan</span>
              </ButtonMain>
              <ButtonMain isDark = {true} onClick = {this.onDelete}>
                <span>Hapus</span>
              </ButtonMain>
            </div>
          </div>
        </LazyFoxModalWindow>

        <LazyFoxModalWindow onDismiss = {this.onDismiss} className={"modal-window-left"} ref = {this.openModalWindowChart} useHeader = {true} headerTitle = {"Tambahkan ke Keranjang"}>
          <div className="success-info ">
            { !state.alreadyAdded &&
              <div className="text-success">
                <h2>Anda akan menambahkan {props.item.title} ke keranjang ?</h2>
                <h2>Stok yang tersedia: {this.stok(props.item?.stok)}</h2>
                <LazyFoxInputBox errorText = {`Maaf stok tidak mencukupi, stok sekarang (${this.stok(props.item?.stok)})`} title = {"Masukan Jumlah yang dibeli"} type = {"number"} key={'total'} {...state.value} targetError ={state.value.isError} onChange = {this.onChange} />

              </div>
            }
             { state.alreadyAdded &&
              <div className="text-success">
                <h2>Selamat {props.item.title} berhasil di masukan ke keranjang</h2>
              </div>
            }
            <ButtonMain isDark = {true} onClick = {state.alreadyAdded? this.goToKeranjang : this.onAddToChart}>
              { !state.alreadyAdded &&
                <span>Tambah Ke Keranjang</span>
              }
              { state.alreadyAdded &&
                <span>Lihat di Keranjang {">"}</span>
              }
            </ButtonMain>
          </div>
        </LazyFoxModalWindow>

      </div>
    )
  }
}
