import { ParsingCommentsDataBundlePropType } from "@interfaces/data-type/type.pages-comments";
import FlatList from "@module-flat-list/index.flat-list";
import { formatRupiah } from "@module-flat-list/index.item-list";
import { CallbackRender, Container, Dispatch, FooterMobileGlobal, HeaderGlobalPages, LazyFoxModalWindow, OwnIcon, StoragesSystem,  } from "index-module";
import React, { Component } from "react";
import { connect } from "react-redux";
import jsonQuery  from 'json-query';
import { RouteComponentProps, withRouter } from "react-router-dom";
import { FlatItemReportList, getDate } from "@module-flat-list/index.item-laporan-list";


export interface ScreenReportPropTypeSmall {
  StoragesSystem: any,
  setStorage?: (obj: ParsingCommentsDataBundlePropType) => void
}

export type ScreenReportPropType = ScreenReportPropTypeSmall & RouteComponentProps;



export interface ScreenReportStateType {
  report: any,
  key: any,
  searchKey: any,
  money?: any,
  isReady?: any,
}

const FormLoginLogicPropDefault : ScreenReportPropTypeSmall = {
  StoragesSystem: {
    report: [

    ]
  },
}

class ScreenReport extends Component<ScreenReportPropType,  ScreenReportStateType> {
  static defaultProps = FormLoginLogicPropDefault;
  private openModalWindow = React.createRef<LazyFoxModalWindow>()
  constructor(props: any){
    super(props)
    this.state = {
      report: props.StoragesSystem?.report || [],
      key: 'title',
      searchKey: "",
      money: {}
    }
  }
  private lastDate: any = new Date().getTime();

  onScroll = () => {


  }

  componentDidUpdate = (prevProps: ScreenReportPropType) => {
    if(this.props.StoragesSystem !== prevProps.StoragesSystem){
      this.lastMoney = 0;
      this.setState({
        report: this.props.StoragesSystem?.report || [],
      })
    }
  }


  onOpen = () => {
    if(this.openModalWindow.current){
      this.openModalWindow.current?.show()
    }
  }

  onDismiss = () => {
    this.setState({isReady: true},() => {
      if(this.openModalWindow){
        this.openModalWindow.current?.dismiss()
      }
    })
  }

  onChangeMoney = (e: any) => {
    this.setState({
      money: e
    })
  }

  getKembalian = (value: any) => {
    if(value < 0){
      return 'Uang Kurang'
    }
    return formatRupiah(value.toString());
  } 

  private timeOut: any= undefined;
  onChange = (e: any) => {
		clearTimeout(this.timeOut)
    this.timeOut = setTimeout(() => {
      this.onChangeRequest(e)
    }, 500)
	}

  onChangeRequest = async(e: any) => {
    this.lastMoney = 0;
    if(e.value.length > 0){
      let query =  await jsonQuery(("report[*"+'textDate'+"~/"+e.value+"/i]"), {
        data: this.props.StoragesSystem,
        allowRegexp: true,
      }) 
      if(query.value.length < 1){
        query =  await jsonQuery(("report[*"+'hour'+"~/"+e.value+"/i]"), {
          data: this.props.StoragesSystem,
          allowRegexp: true,
        });
        if(query.value.length < 1){
          query =  await jsonQuery(("report[*"+'totalPayment'+"~/"+e.value+"/i]"), {
            data: this.props.StoragesSystem,
            allowRegexp: true,
          })
        }
      }
      
      this.setState({
        searchKey: query.value,
        report: query.value
      })
    }else{
      this.setState({
        report: this.props.StoragesSystem.report
      })
    }
  }

  calculateDate = (item: any, index: any, arr: any) => {
    if(index > 0){
      const datePrev = new Date(arr[index-1]?.date).setHours(0,0,0,0);
      const dateCurrent = new Date(item?.date).setHours(0,0,0,0);
      let gap = Math.floor((dateCurrent-datePrev)/(24*3600*1000)) ;
      gap = gap < 0 ? (gap * -1) : gap;
      if(gap > 0){
        return true
      }
    }
    return false;
  }

  private lastMoney: any = 0;
  calculateMoney = (item: any, isHasGap: any) => {
    let tempMoney = this.lastMoney;
    if(isHasGap){
      this.lastMoney = parseInt(item.totalPayment);
    }else{
      // if(!isLast){
        this.lastMoney += parseInt(item.totalPayment);
      // }
      tempMoney  = this.lastMoney;

    }
    
    return tempMoney;
  }

  render(){
    const { props, state } = this;
    
    return(
      <>
        <HeaderGlobalPages title = {"Laporan"} useBack = {true} onChange = {this.onChange} />
        <Container className={"login_page_wrapper"}>
          <section className={"content_value content_value_login"}>
            
            <FlatList data = {state.report} onScroll={this.onScroll} render= { ({items, index, arr} : CallbackRender) => {
              const isHasGap = this.calculateDate(items, index, arr);
              const totalPayment: any = this.calculateMoney(items, isHasGap);
              const lastPayment: any = (isHasGap && index >= arr.length -1 )? items.totalPayment : totalPayment;
              
              return (
                <>
                  { (isHasGap) &&
                    <div className="total-revenuew">
                      <div className="total-text">
                        <span>Total Pendapatan Tanggal <span>( {getDate(arr[index-1]?.date, "text")})</span> </span>
                        <h2>{formatRupiah(totalPayment?.toString())}</h2>
                      </div>
                    </div>
                  }
                  <FlatItemReportList {...props} item = {items} index = {index} key = {index} isreport = {true} />
                  { (index >= arr.length -1) &&
                    <div>{formatRupiah(lastPayment?.toString())}</div>
                  }
                </>
              )
            }}/>
            {state.report.length < 1 &&
              <div className="error-list-empty">
                <div className="error-list-empty-inside">
                  <OwnIcon.IconAlert />
                  <h1>Laporan anda kosong</h1>
                </div>
              </div>
            }
          </section>
        </Container>
        <FooterMobileGlobal {...props}  activePage = {"laporan"}  />
      </>
    )
  }
}

const mapStateToProps = (state: any) => ({
  StoragesSystem: state.StoragesSystem,
})
const mapDispatchToProps = (dispatch: Dispatch ) => ({
  setStorage: ({ data, type }: ParsingCommentsDataBundlePropType) => dispatch( new StoragesSystem().setStorage({ data, type }) ),
})

export default withRouter(connect( mapStateToProps, mapDispatchToProps )( ScreenReport ));
