import React, { FunctionComponent } from 'react';
import { OwnIcon } from '@icons/module-icons'; 

export interface SearchBoxPropType{
  onChange?: ( event: any ) => void,
  placeholder?: string,
  searchType?: string,
}

export const SearchBoxPropDefault: SearchBoxPropType = {
  placeholder: 'Pencarian',
  onChange: ( event: any)=> void(0),
  searchType: 'input',
}

const SearchBox: FunctionComponent<SearchBoxPropType> = ( props ) => {
  const onChange = (event: any) => {
    if(props.onChange){
      props.onChange({event,value:event.target.value});
    }
  }
  if( props.searchType !== 'button' ){
    return (
      <div className={"input_search_box"}>
        <div className={"input_box"} >
          <input className={"inputs"} onChange={onChange} placeholder={props.placeholder}></input>
          <OwnIcon.IconSearch className={"icon_search"} />
        </div>
      </div>
    )
  }else{
    return (
      <div className={"input_search_box"}>
        <div className={"input_box"} >
          <div className={"inputs"} >{props.placeholder}</div>
          <OwnIcon.IconSearch className={"icon_search"} />
        </div>
      </div>
    )
  }
}

SearchBox.defaultProps = SearchBoxPropDefault;

export  { SearchBox };
