interface FlatListStateType{
  isActive?: boolean,
  isDelete?: any,
  value?: any,
  isChart?: boolean,
  alreadyAdded?: boolean,
  isGoto?: boolean,
  isError?: boolean,
}

const FlatListPropDefault : any = {

} 

const ENUM_DAY = ['Senin', 'Selasa', 'Rabu', 'Kamis', 'Jumat', 'Sabtu', 'Minggu'];
const ENUM_MONTH = ['Januari', 'Februari', 'Maret', 'April', 'Mei', 'Juni', 'Juli', 'Agustus', 'September', 'Oktober', 'November', 'Desember'];

export const getDate = (dates: any, type: string = 'text') => {
  const tempDate = new Date(dates);
  const day = tempDate.getDay();
  const date = tempDate.getDate();
  const month = tempDate.getMonth();
  const year = tempDate.getFullYear();
  const minute = tempDate.getMinutes();
  const hour = tempDate.getHours();
  const second = tempDate.getSeconds();

  if(type==='text'){
    return `${ENUM_DAY[day]}, ${date} ${ENUM_MONTH[month]} ${year}`  
  }
  if(type==='hour'){
    return `${hour}:${minute}:${second}`  
  }
  if(type==='normal'){
    return `${ENUM_DAY[day]}, ${date}/${(month+1)}/${year}` 
  }
}
export class FlatItemReportList extends React.Component<any, FlatListStateType>{
  constructor( props: any ){
    super( props );
    this.state = {
      isDelete: false,
      alreadyAdded: false,
    }
  }
  public static defaultProps = FlatListPropDefault;
  private openModalWindow = React.createRef<LazyFoxModalWindow>()
  private openModalWindowChart = React.createRef<LazyFoxModalWindow>()
  

  onBack = () => {
    this.setState({isDelete: false})
    if(this.openModalWindow.current){
      this.openModalWindow.current?.dismiss()
    }
  }

  onOpenPupup = () => {
    if(this.openModalWindow.current){
      this.openModalWindow.current?.show()
    }
  }

  onOpenPupupChart = () => {
    if(this.openModalWindowChart.current){
      this.openModalWindowChart.current?.show()
    }
  }
  onClosed =() => {
    if(this.openModalWindowChart.current){
      this.openModalWindowChart.current?.dismiss()
    }
  }

  onDelete = (e: any) => {
    if(this.openModalWindow){
      this.setState({isDelete: true}, () => {
        this.openModalWindow.current?.dismiss()
      })
    }
  }
  
  

  onDismiss = () => {
    const { state } = this;
    this.setState({
      isGoto: false,
      isDelete: false,
    }, () => {
      if(state.isDelete){
        const { props} = this;
        const key = 'report'
        let catalog = this.props.StoragesSystem[key] || [];
        catalog.splice(props.index, 1);
        if(this.props.setStorage){
          this.props.setStorage({data: catalog, type: key});
        }
      }
    })
  }

  render(){
    const { props, state } = this;
    return (
      <div className={"item-list-flat-list"} key = {props.key}>
        <div className={"item-list-flat-list-inside"}>
          <div className={"medicine-title"} onClick = {this.onOpenPupupChart} >
            <span><span className='numbering'>{(props.index+1)}</span>{getDate(props.item.date, "text")} {getDate(props.item.date, "hour")}</span>
          </div>
          <div className={"medicine-content"}>
            <div className={'main-contents'}>
              <div className={"info"} onClick = {this.onOpenPupupChart}>
                <>
                <span className={"price"}>Jam: <span>{getDate(props.item.date, "hour")}</span></span>
                  <span className={"price"}>Total Barang Dibeli: <span>{props.item.chart.length}</span></span>
                  <span className={"stok"}>Total Uang: <span>{formatRupiah(props.item?.totalPayment)}</span></span>
                </>
                <div className={"lihat"} >Lihat Detailnya {">"}</div>
              </div>
              <div className={"editor-container"}>
                <span onClick={ this.onOpenPupup}>
                  <Trash />
                </span>
              </div>
            </div>
          </div>
        </div>

        <LazyFoxModalWindow onDismiss = {this.onDismiss} className={"modal-window-center"} ref = {this.openModalWindow} useHeader = {true} headerTitle = {"PERINGATAN !!!"}>
          <div className="success-info">
            <div className="text-success">
              <h2>Apakah anda yakin ingin menghapus Laporan padat tanggal {props.item.title} ?</h2>
              <span>{getDate(props.item.date, "text")} {getDate(props.item.date, "hour")}</span>
            </div>
            <div className='d_flex justify_content_between'>
              <ButtonMain className='dark-key' isDark = {true} onClick = {this.onBack}>
                <span>Batalkan</span>
              </ButtonMain>
              <ButtonMain isDark = {true} onClick = {this.onDelete}>
                <span>Hapus</span>
              </ButtonMain>
            </div>
          </div>
        </LazyFoxModalWindow>

        <LazyFoxModalWindow className={"modal-window-left custom-side-bar"} ref = {this.openModalWindowChart} useHeader = {true} headerTitle = {"Laporan"}>
          <div className="success-info ">
            <div className="text-success">
              <span>Daftar Keranjang Pada</span>
              <h2>{getDate(props.item.date, "text")} {getDate(props.item.date, "hour")}</h2>
              <span>Total Harga</span>
              <h2>{formatRupiah(props.item?.totalPayment)}</h2>
              <FlatList data = {props.item.chart} render= { ({items, index} : CallbackRender) => (
                <>
                  <FlatItemList {...props} item = {items} index = {index} isHide = {true} key = {`${index}-chart-${items.id}`} isChart = {true} />
                </>
              )} />
            </div>
            <br/>
            <ButtonMain isDark = {true} onClick = {this.onClosed }>
            <span>Tutup</span>
            </ButtonMain>
          </div>
        </LazyFoxModalWindow>

      </div>
    )
  }
}
